import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { NavController, NavParams, App } from 'ionic-angular';


@Component({
  selector: 'page-payment-made',
  templateUrl: 'payment-made.html',
})
export class PaymentMadePage {
  details: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: HttpClient, private app: App) {
    this.details = this.navParams.get('details');
    this.initPay();
  }

  continue() {
    const startIndex = this.navCtrl.getActive().index - 1;
    this.navCtrl.remove(startIndex, 2).then(() => {
      this.navCtrl.setRoot('TabsPage');
    });
  }

  initPay() {
    this.http.post('https://realm-glory.herokuapp.com/api/giving', {
      name: this.details.name, 
      email: this.details.email, 
      phone: this.details.phone, 
      type: this.details.type, 
      amount: this.details.amount, 
      ref: this.details.ref
    }).subscribe((data) => {
      console.log('received', data);
    });
  }

}



